import React from 'react';
import Result from './Result.js';
import Card from 'react-bootstrap/Card';
import Form from 'react-bootstrap/Form';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import ListGroup from 'react-bootstrap/ListGroup';
import DropdownButton from 'react-bootstrap/DropdownButton';
import Dropdown from 'react-bootstrap/Dropdown';
import './ProductLinks.css';

class ProductLinks extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showingResult: false,
      inputUrl: "",
      resultUrl: "",
      error: false,
      region: "Canada"
    };
  }

  componentDidMount() {
    this.props.setNavSelected("Product Links");
  }

  showResult = () => {
    this.setState({error: false});
    let inputUrl = this.state.inputUrl;

    if (inputUrl.indexOf('amazon') === -1) {
      this.setState({error: true});
    }

    let idx = inputUrl.indexOf(`/dp/`);
    let offset = 0;
    if (idx >= 0) {
      offset = 4;
    } else {
      idx = inputUrl.indexOf(`/gp/product/`);
      if (idx >= 0) {
        offset = 12;
      }
    }

    let ASIN = '';
    if (offset !== 0) {
      idx += offset;
      ASIN = inputUrl.substr(idx);
      if (ASIN.length < 10) {
        this.setState({error: true});
      } else {
        ASIN = ASIN.substr(0, 10);
      }
    } else {
      this.setState({error: true});
    }

    let resultUrl = '';
    switch (this.state.region) {
      case "Canada":
        resultUrl = `https://www.amazon.ca/dp/` + ASIN +
          `?ie=UTF8&camp=1789&creative=9325&creativeASIN=` + ASIN + `&linkCode=am2&tag=puzzlefight02-20`;
        break;
      case "US":
        resultUrl = `https://www.amazon.com/gp/offer-listing/` + ASIN +
          `/ref=as_li_tl?ie=UTF8&camp=1789&creative=9325&creativeASIN=` + ASIN + `&linkCode=am2&tag=puzzlefight09-20`;
        break;
      case "UK":
        resultUrl = `https://www.amazon.co.uk/gp/product/` + ASIN +
          `?ie=UTF8&tag=puzzlefighter-21&camp=1634&linkCode=xm2&creativeASIN=` + ASIN;
        break;
      case "France":
        resultUrl = `https://www.amazon.fr/gp/product/` + ASIN +
          `?ie=UTF8&tag=puzzlefight0e-21&camp=1642&linkCode=xm2&creativeASIN=` + ASIN;
        break;
      case "Germany":
        resultUrl = `https://www.amazon.de/gp/product/` + ASIN +
          `?ie=UTF8&tag=puzzlefight00-21&camp=1638&linkCode=xm2&creativeASIN=` + ASIN;
        break;
      case "Spain":
        resultUrl = `https://www.amazon.es/gp/product/` + ASIN +
          `?ie=UTF8&tag=puzzlefight0c-21&camp=3638&linkCode=xm2&creativeASIN=` + ASIN;
        break;
      case "Italy":
        resultUrl = `https://www.amazon.it/gp/product/` + ASIN +
          `?ie=UTF8&tag=puzzlefight02-21&camp=3414&linkCode=xm2&creativeASIN=` + ASIN;
        break;
      case "Japan":
        resultUrl = `https://www.amazon.co.jp/gp/product/` + ASIN +
          `?ie=UTF8&tag=puzzlefighter-22&camp=247&linkCode=xm2&creativeASIN=` + ASIN;
        break;
      default:
        this.setState({error: true});
        break;
    }

    this.setState({
      resultUrl: resultUrl,
      showingResult: true
    });
  }

  selectRegion = (e) => {
    this.setState({region: e});
  }

  render() {
    return (
      <ListGroup variant="flush" className={"product-links"}>
        <ListGroup.Item>
          <Container>
            <Card.Text style={{display: "inline"}}>
              Select a region:
            </Card.Text>
            <DropdownButton id="product-region-dropdown" title={this.state.region} onSelect={this.selectRegion}>
              <Dropdown.Item eventKey="Canada">Canada</Dropdown.Item>
              <Dropdown.Item eventKey="US">US</Dropdown.Item>
              <Dropdown.Item eventKey="UK">UK</Dropdown.Item>
              <Dropdown.Item eventKey="France">France</Dropdown.Item>
              <Dropdown.Item eventKey="Germany">Germany</Dropdown.Item>
              <Dropdown.Item eventKey="Spain">Spain</Dropdown.Item>
              <Dropdown.Item eventKey="Italy">Italy</Dropdown.Item>
              <Dropdown.Item eventKey="Japan">Japan</Dropdown.Item>
            </DropdownButton>
            <Card.Text>
              Enter the URL of the Amazon product you would like to purchase:
            </Card.Text>
            <Row>
              <Col lg="9" md="8" xs="12">
                <Form.Control type="text" placeholder="Product URL"
                  onChange={e => this.setState({ inputUrl: e.target.value })}/>
              </Col>
              <Col lg="3" md="4" xs="12">
                <Button variant="primary" onClick={this.showResult}>Get Shop Link</Button>
              </Col>
            </Row>
          </Container>
        </ListGroup.Item>
        {this.state.showingResult ? <Result resultUrl={this.state.resultUrl} error={this.state.error}/> : null}
      </ListGroup>
    );
  }
}

export default ProductLinks;
