import React from 'react';
import Card from 'react-bootstrap/Card';
import ListGroup from 'react-bootstrap/ListGroup';

class Result extends React.Component {
  render() {
    return (
      this.props.error ?
        <ListGroup.Item>
          <Card.Text style={{color: "firebrick"}}>
            Error: The URL you have entered is not valid.
          </Card.Text>
        </ListGroup.Item> :
        <ListGroup.Item>
          <Card.Text>
            The shop link is:
          </Card.Text>
          <Card.Text>
            <a href={this.props.resultUrl} target="_blank" rel="noreferrer">{this.props.resultUrl}</a>
          </Card.Text>
        </ListGroup.Item>
    );
  }
}

export default Result;
