import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import ProductLinks from './ProductLinks/ProductLinks.js';
import TrialLinks from './TrialLinks/TrialLinks.js';
import Card from 'react-bootstrap/Card';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import {Redirect} from "react-router";
import {BrowserRouter as Router, Switch, Route} from "react-router-dom";

class App extends React.Component {
  setNavSelected(pageName) {
    let cardHeader = document.getElementsByClassName("card-header")[0];
    let links = cardHeader.getElementsByClassName("mr-auto")[0].children;
    for (let i = 0; i < links.length; i++) {
      if (links[i].innerHTML === pageName) {
        links[i].style.color = "rgba(0, 0, 0, 1)";
      } else {
        links[i].style.color = "rgba(0, 0, 0, .5)";
      }
    }
  }

  render() {
    return (
      <div className="App">
        <Card>
          <Card.Header>
            <h3>Welcome to Puzzlefighter's Amazon Shop!</h3>
            <Navbar bg="light" variant="light">
              <Nav className="mr-auto">
                <Nav.Link href="productlinks">Product Links</Nav.Link>
                <Nav.Link href="triallinks">Trial Links</Nav.Link>
              </Nav>
            </Navbar>
          </Card.Header>
          <Router>
            <Switch>
              <Route exact path="/" render={() => {return <Redirect to="/productlinks"/>}}/>
              <Route path="/productlinks">
                <ProductLinks setNavSelected={this.setNavSelected} />
              </Route>
              <Route path="/triallinks">
                <TrialLinks setNavSelected={this.setNavSelected} />
              </Route>
            </Switch>
          </Router>
        </Card>
      </div>
    );
  }
}

export default App;
